import React, { useEffect, useMemo, useState } from 'react'
import { DepartmentTable } from '../components/admin/department'
import Button from '../components/commanComponent/Button'
import { useDispatch, useSelector } from 'react-redux'
import { departmentAction } from '../core/resource/actions'
import CreateUpdateDepartment from '../components/modal/admin/department/CreateUpdateDepartment'
import ApproveRejectModal from '../components/modal/ApproveRejectModal'
import { pagination } from '../utils/Constants.config'

const Department = () => {
  const dispatch = useDispatch();
  const { getAllDepartmentRes, getAllDepartmentError, getAllDepartmentPending } = useSelector(state => state.department);
  const { deleteDepartmentPending, deleteDepartmentRes, deleteDepartmentError } = useSelector(state => state.department);
  const [departmentList, setDepartmentList] = useState('')
  const [selectedDepartmentData, setSelectedDepartmentData] = useState('')
  const [openCreateUpdateDepModal, setOpenCreateUpdateDepModal] = useState(false);
  const [deleteDepId, setDeleteDepId] = useState()
  const [isShowDeleteModal, setIsShowDelelteModal] = useState(false)
  const [currentPage, setCurrentPage] = useState(1);

  const toggleDepartmentModal = () => {
    setOpenCreateUpdateDepModal(!openCreateUpdateDepModal);
  };

  // useEffect(() => {
  //   getAllDepartments()
  // }, [])
  useEffect(() => {
    getAllDepartments()
  }, [currentPage])

  const getAllDepartments = () => {
    const params = {
      page: currentPage,
      limit: pagination.limit,
    }
    dispatch(departmentAction.getAllDepartmentRequest(params));
  }

  useEffect(() => {
    if (!getAllDepartmentPending) {
      if (getAllDepartmentError) {
        return;
      }
      if (getAllDepartmentRes) {
        if (getAllDepartmentRes?.data?.code === 200) {
          setDepartmentList(getAllDepartmentRes?.data?.data?.data)
        }
      }
    }
  }, [
    getAllDepartmentRes,
    getAllDepartmentError,
    getAllDepartmentPending
  ]);

  // Delete Department......
  const onDeleteCancelDepartment = (type) => {
    if (type) {
      const params = {
        _id: deleteDepId
      }
      dispatch(departmentAction?.deleteDepartmentRequest(params))
      setIsShowDelelteModal(!isShowDeleteModal)
    } else {
      setIsShowDelelteModal(!isShowDeleteModal)
    }
  }
  const onHandleDeleteModal =(id)=>{
    setIsShowDelelteModal(!isShowDeleteModal)
    setDeleteDepId(id)
  }

  useEffect(() => {
    if (!deleteDepartmentPending) {
      if (deleteDepartmentError) {
        dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_DELETE_DEPARTMENT_STORE'))
        return;
      }
      if (deleteDepartmentRes) {
        if (deleteDepartmentRes?.data?.code == 200) {
          dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_DELETE_DEPARTMENT_STORE'))
          getAllDepartments()
        }
      }
    }
  }, [
    deleteDepartmentRes,
    deleteDepartmentError,
    deleteDepartmentPending
  ]);

  const memoizedDepartmentList = useMemo(() => departmentList, [departmentList]);

  return (
    <div>
      <div className="d-flex  justify-content-end ">
        <Button btnClassName={"btn btn-primary gradientBg"}
          onClickFun={() => toggleDepartmentModal()}
        >Create New Department</Button>
      </div>
      <DepartmentTable
        departmentList={memoizedDepartmentList}
        isLoading={getAllDepartmentPending}
        toggleDepartmentModal={toggleDepartmentModal}
        setSelectedDepartmentData={setSelectedDepartmentData}
        onHandleDeleteModal={onHandleDeleteModal}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />

      <CreateUpdateDepartment
        getAllDepartments={getAllDepartments}
        setSelectedDepartmentData={setSelectedDepartmentData}
        openCreateUpdateDepModal={openCreateUpdateDepModal}
        toggleDepartmentModal={toggleDepartmentModal}
        selectedDepartmentData={selectedDepartmentData}
      />

      {isShowDeleteModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteCancelDepartment}
          isShow={isShowDeleteModal}
          desc={"You want to delete this Department"}
          isLoading={deleteDepartmentPending}
        />)
      }
    </div>
  )
}

export default Department
