import ValidattionTranslation from './ValidattionTranslation';

const trans = (key,replacements = {}) => {
let translation = ValidattionTranslation[key] || key;
for (const [placeholder, value] of Object.entries(replacements)) {
  translation = translation.replace(new RegExp(`\\$${placeholder}`, 'g'), value);
}
return translation;
}
const emailRegix =
  /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const panNumRegex = new RegExp(/^[A-Z]{5}[0-9]{4}[A-Z]{1}$/);
const onlyDigitRegix = /^\d+$/;

const isFieldValid = (formik, name) =>
  !!(formik.touched[name] && formik.errors[name]);

const getFieldErrorMsg = (formik, name) => {
  return (
    isFieldValid(formik, name) && (
      <small className="p-error">{formik.errors[name]}</small>
    )
  );
};

const digitsOnly = (value) => {
  return onlyDigitRegix.test(value);
};

const validateResponse = () => {
  return { status: 401, msg_code: 401, msg: "", isValid: false, errors: null };
};

const isBlank = (value) => {
  return value === "" || false;
};

const isNull = (value) => {
  return value === null || false;
};

const isNun = (value) => {
  return Number.isNaN(value) || false;
};

const isBlankOrNull = (value) => {
  return value === undefined || isBlank(value) || isNull(value) || false;
};

const validateMobile = (mobile) => {
  if (mobile.length === 0) {
    return {
      isValid: false,
      msg: trans("validations.fieldReq").replace(
        "$FIELD",
        trans("label.mobileNum")
      ),
    };
  }
  if (mobile.length < 10) {
    return {
      isValid: false,
      msg: trans("validations.minLength").replace("$1", 10),
    };
  }
  if (mobile.length > 10) {
    return {
      isValid: false,
      msg: trans("validations.maxLength").replace("$1", 10),
    };
  }
  return { isValid: true, msg: "" };
};

const validateEmail = (email) => {
  if (email.length === 0) {
    return {
      isValid: false,
      msg: trans("validations.fieldReq").replace(
        "$FIELD",
        trans("label.emailAddress")
      ),
    };
  }
  if (!emailRegix.test(email)) {
    return { isValid: false, msg: trans("validations.emailInvalid") };
  }
  return { isValid: true, msg: "" };
};

const validateSchema = async (schema, data) => {
  const validateDataRes = validateResponse();
  if (!schema) {
    validateDataRes.msg_code = 401;
    validateDataRes.msg = trans("validations.allFieldReq");
    return validateDataRes;
  }

  const result = await schema.isValid(data);
  const errorsList = {};
  if (!result) {
    await schema.validate(data, { abortEarly: false }).catch((err) => {
      const errors = err.inner.reduce((acc, error) => {
        return (errorsList[error.path] = error.message);
      }, {});
    });
    validateDataRes.errors = errorsList;
    return validateDataRes;
  }
  validateDataRes.msg_code = 200;
  validateDataRes.status = 200;
  validateDataRes.isValid = true;
  return validateDataRes;
};

const validateAddBranch = async (body) => {
  const validateDataRes = await validateSchema("addOrgBranchSchema", body);
  return validateDataRes;
};
const validateAddOrganization = async (body) => {
  const validateDataRes = await validateSchema("addOrganizationSchema", body);
  return validateDataRes;
};

const validateOrgSettings = async (body) => {
  const validateDataRes = await validateSchema("organizationSettingSchema", body);
  return validateDataRes;
};

const validateAddInvoice = async (body) => {
  const validateDataRes = await validateSchema("addInvoiceSchema", body);
  return validateDataRes;
};
const regex = /^[A-Za-z0-9 ' ']+$/;
const validateField = (value, type, minLength = 3) => {
  if (type === "mobile") {
    if (value.length < 10) {
      return trans("minLength").replace("$1", 10);
    }
    if (value.length > 10) {
      return trans("maxLength").replace("$1", 10);
    }
    if(!onlyDigitRegix.test(value)){
      return trans("onlyDigit")
    }
    
  } else if (type === "email") {
    if (!emailRegix.test(value)) {
      return trans("emailInvalid");
    }
  } else if (type === "string") {
    if (value.length < minLength) {
      return trans("minLength").replace("$1", minLength);
    }else if(!regex.test(value)){
      return trans("shiftName");
    }
  }

  return true;
};

export {
  trans,
  digitsOnly,
  isFieldValid,
  getFieldErrorMsg,
  validateAddBranch,
  validateMobile,
  validateAddOrganization,
  validateOrgSettings,
  validateAddInvoice,
  validateSchema,
  validateField,
};
