import React, { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton } from '../../../globalComponents';
import { departmentAction, notificationActions } from '../../../../core/resource/actions';
import { toast } from 'react-toastify';
import { addDepartment, addDepartmentSchema } from '../../../../utils/ValidationSchema';
import * as yup from 'yup';
import { validateSchema } from '../../../../utils/ValidationHelper';
import CustomError from '../../../globalComponents/CustomError';
import { notify } from '../../../globalComponents/ToastNotification';

const CreateUpdateDepartment = (props) => {
    const { 
        getAllDepartments, 
        openCreateUpdateDepModal, 
        toggleDepartmentModal, 
        selectedDepartmentData,
        setSelectedDepartmentData
     } = props
    const [errors, setErrors] = useState({});
    const dispatch = useDispatch();
    const { 
        addDepartmentPending, 
        addDepartmentRes, 
        addDepartmentError, 
        updateDepartmentPending, 
        updateDepartmentRes, 
        updateDepartmentError 
    } = useSelector(state => state.department);
    const [isActive, setIsActive] = useState(false);
    const [departmentId, setDepartmentId] = useState('')
    const [depData, setDepData] = useState({
        depName: '',
        shortName: '',
    })
    

    const createDepartment = async () => {
        const validationResponse = await validateSchema(addDepartmentSchema, depData);
        if (validationResponse.isValid) {
            setErrors({});
            const params = {
                name: depData?.depName || '',
                short_name: depData?.shortName || '',
                is_active: isActive ? true:false  
            }
            if (selectedDepartmentData) {
                params['_id'] = departmentId
                dispatch(departmentAction.updateDepartmentRequest(params));
            } 
            else{
                dispatch(departmentAction.addDepartmentRequest(params));
            }

        } else {
            setErrors(validationResponse.errors);
        }

    }

    useEffect(() => {
        if (!addDepartmentPending) {
            if (addDepartmentError) {
                dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_ADD_DEPARTMENT_STORE'))
                return;
            }
            if (addDepartmentRes) {
                if (addDepartmentRes?.data?.code == 200) {
                    dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_ADD_DEPARTMENT_STORE'))
                    resetDepartmentData()
                    getAllDepartments();
                   
                }
            }
        }
    }, [
        addDepartmentPending,
        addDepartmentRes,
        addDepartmentError,
    ]);

    useEffect(() => {
        if (!updateDepartmentPending) {
          if (updateDepartmentError) {
            dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_UPDATE_DEPARTMENT_STORE'))
            return;
          }
          if (updateDepartmentRes) {
            if (updateDepartmentRes?.data?.code ==200) {
              dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_UPDATE_DEPARTMENT_STORE'))
              getAllDepartments();
            }
          }
        }
      }, [
        updateDepartmentPending, 
        updateDepartmentRes, 
        updateDepartmentError
      ]);

    const handleChange = (event) => {
        errors && setErrors({});
        const { name, value } = event.target
        setDepData({ ...depData, [name]: value })
    }

    //update Departemnt 
    useEffect(() => {
        setDepData({
            depName: selectedDepartmentData?.name || '',
            shortName: selectedDepartmentData?.short_name || '',
        })
        setIsActive(selectedDepartmentData?.is_active)
        setDepartmentId(selectedDepartmentData?._id)
    }, [selectedDepartmentData])

    useEffect(()=>{
        if(openCreateUpdateDepModal ==false){
            resetDepartmentData()
        }
    },[openCreateUpdateDepModal])


    const resetDepartmentData =()=>{
        setDepData({
            depName:  '',
            shortName: '',
        })
        setSelectedDepartmentData('')
        setErrors({});
    }

    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${openCreateUpdateDepModal ? 'show' : ''}`} tabIndex={-1}
                style={{ visibility: openCreateUpdateDepModal ? 'visible' : 'hidden' }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary"> {selectedDepartmentData ?"Update":"Create New"} Department</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => toggleDepartmentModal()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Name <span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <input type="text" className="form-control fs-7"
                                        placeholder="Enter Department Name"
                                        name='depName'
                                        value={depData?.depName || ''}
                                        onChange={(e) => handleChange(e)}
                                    />
                                    {errors?.depName && (
                                        <CustomError text={errors.depName} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Short Name <span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <input type="text" className="form-control fs-7"
                                        name='shortName'
                                        placeholder="Enter Short Name"
                                        value={depData?.shortName}
                                        onChange={(e) => handleChange(e)}

                                    />
                                    {errors?.shortName && (
                                        <CustomError text={errors.shortName} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Is Active</label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <input type="checkbox" placeholder="Is Active"
                                        checked={isActive}
                                        onChange={(e) => setIsActive(!isActive)}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="d-flex align-items-center justify-content-end gap-2">
                            <CustomButton
                                type="button"
                                classNames={"btn btn-primary gradientBg"}
                                handleClick={e => createDepartment()}
                                isLoading={selectedDepartmentData?updateDepartmentPending:addDepartmentPending}
                                text={`${selectedDepartmentData?"Update":"Create"} Department`}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateUpdateDepartment
